import { useState } from 'react';
import {
  ButtonGroup,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  makeStyles
} from '@material-ui/core';

import InfoButton from '../Template/Info';

const useStyles = makeStyles(() => ({
  root: {
    marginTop: '10px',
    marginRight: '10px',
    alignItems: 'center',
  },
  infoButton: {
    marginLeft: '5px',
  },
}))

function Info(props) {
  const { open, setOpen } = props;

  const handleClose = () => {
    setOpen(false);
  }

  return (
    <Dialog
      open={open}
      onClose={handleClose}
    >
      <DialogTitle>What are "targeted" and "traditional" contributions?</DialogTitle>
      <DialogContent>
        <DialogContentText>
          A "targeted" contribution uses information about your asset balances/shares to target
          your contribution towards bringing your assets closer to your desired asset mix. The
          purpose of this is to attempt to minimize the amount of capital gains taxes you would
          owe when you later rebalance your portfolio. This strategy may also sell tax-exempt
          assets in furtherance of this goal.
        </DialogContentText>
        <DialogContentText>
          A "traditional" contribution is how people more traditionally contribute to their
          portfolio. It ignores your asset balances/shares and instead will suggest contributing
          an amount to each asset proportionate to your desired asset mix.
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button color='primary' onClick={handleClose}>Close</Button>
      </DialogActions>
    </Dialog>
  );
}

export default function Strategy(props) {
  const classes = useStyles();

  const { contribute, setEdited, strategy, setStrategy } = props;

  const [openInfo, setOpenInfo] = useState(false);

  const setColor = (selected) => selected ? 'primary' : 'default';

  const onClick = (state) => () => {
    if (strategy !== state) {
      setEdited(true);
    }
    window.localStorage.setItem('strategy', state);
    setStrategy(state);
  }

  return (
    <div className={classes.root}>
      <ButtonGroup
        style={{ ...(contribute === false ? { display: 'none' } : {}) }}
      >
        <Button
          variant='contained'
          color={setColor(strategy === 'targeted')}
          onClick={onClick('targeted')}
        >
          Targeted
        </Button>
        <Button
          variant='contained'
          color={setColor(strategy === 'traditional')}
          onClick={onClick('traditional')}
        >
          Traditional
        </Button>
      </ButtonGroup>
      <InfoButton
        className={classes.infoButton}
        onClick={() => setOpenInfo(true)}
        style={{ ...(contribute === false ? { display: 'none' } : {}) }}
      />
      <Info open={openInfo} setOpen={setOpenInfo} />
    </div>
  );
}