import React from 'react';
import {
  makeStyles,
  Grid,
  ThemeProvider,
} from '@material-ui/core';
import ReactGA from 'react-ga';

import theme from './theme';

import HeadBanner from './components/HeadBanner';
import Footer from './components/Footer';
import ContribWithdrawField from './components/ContribWithdrawField';
import AssetList from './components/AssetList';
import Controls from './components/Controls';

import { getDefault } from './utils';
import { pullStorage } from './utils/io';

if (process.env.NODE_ENV && process.env.NODE_ENV !== 'development') {
  ReactGA.initialize('UA-200763466-1');
  ReactGA.pageview(window.location.pathname + window.location.search);
}

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'flex-start',
    minHeight: '100vh',
    backgroundColor: 'rgb(245, 245, 245)'
  },
  header: {
    width: '100%',
  },
  main: {
    // backgroundColor: '#282c34',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'left',
    color: 'white',
    width: '100%',
    maxWidth: '1000px',
    marginLeft: 'auto',
    marginRight: 'auto',
  },
});

function App() {
  const classes = useStyles();

  const [assetData, setAssetData] = React.useState(
    pullStorage('assetData', JSON.parse) || [getDefault()]
  );
  const [edited, setEdited] = React.useState(true);

  const initContribute = () => {
    const value = pullStorage('contribute', JSON.parse);
    if (value === null || value) {
      return true;
    }
    return false;
  };

  const [contribute, setContribute] = React.useState(
    initContribute(),
  );
  const [amount, setAmount] = React.useState(
    pullStorage('amount', JSON.parse) || 0.0,
  );
  const [strategy, setStrategy] = React.useState(
    pullStorage('strategy') || 'targeted',
  );
  const [remainder, setRemainder] = React.useState(undefined);
  const [withdrawn, setWithdrawn] = React.useState(undefined);
  const [lastCalculation, setLastCalculation] = React.useState(undefined);

  const addAsset = () => {
    setAssetData(assetData.concat([getDefault()]));
  };

  const updateAssetData = (updates, index) => {
    const cloned = assetData.slice();
    cloned[index] = {
      ...assetData[index],
      ...updates,
    };
    setAssetData(cloned);
    setEdited(true);
    window.localStorage.setItem('assetData', JSON.stringify(cloned));
  };

  const deleteAsset = (index) => {
    const cloned = assetData.slice();
    cloned.splice(index, index === 0 ? index + 1 : index);
    setAssetData(cloned);
    setEdited(true);
    window.localStorage.setItem('assetData', JSON.stringify(cloned));
  };

  return (
    <ThemeProvider theme={theme}>
      <div className={classes.root}>
        <header className={classes.header}>
          <HeadBanner />
        </header>
        <main className={classes.main}>
          <Grid container spacing={3}>
            <ContribWithdrawField
              contribute={contribute}
              setContribute={setContribute}
              amount={amount}
              setAmount={setAmount}
              remainder={remainder}
              withdrawn={withdrawn}
              lastCalculation={lastCalculation}
              edited={edited}
              setEdited={setEdited}
              strategy={strategy}
              setStrategy={setStrategy}
            />
          </Grid>
          <AssetList
            assetData={assetData}
            updateAssetData={updateAssetData}
            deleteAsset={deleteAsset}
            edited={edited}
          />
          <Controls
            assetData={assetData}
            contribute={contribute}
            amount={amount}
            strategy={strategy}
            setAssetData={setAssetData}
            addAsset={addAsset}
            updateAssetData={updateAssetData}
            setRemainder={setRemainder}
            setWithdrawn={setWithdrawn}
            setLastCalculation={setLastCalculation}
            setEdited={setEdited}
          />
        </main>
        <Footer />
      </div>
    </ThemeProvider>
  );
}

export default App;
