import React from 'react';

import { Button, CircularProgress, makeStyles } from '@material-ui/core';
import { keyBy } from 'lodash';
import { post } from 'axios';

import { convertAssetForRequest, convertAssetForResponse } from '../../utils';
import { validateSubmission } from '../../utils/validation';
import errorWrapper from '../ErrorWrapper';
import { axiosError } from '../../utils/errors';

const useStyles = makeStyles(() => ({
  button: {
    marginBottom: '5px',
  },
  buttonContent: {
    display: 'flex',
    alignItems: 'center',
  },
  thinking: {
    color: 'rgb(230,230,230)',
  },
  buttonText: {
    paddingLeft: '10px',
    paddingRight: '30px',
  },
}));

function RebalanceButton(props) {
  const classes = useStyles();

  const [thinking, setThinking] = React.useState(false);

  const {
    assetData,
    contribute,
    amount,
    setAssetData,
    setRemainder,
    setWithdrawn,
    setLastCalculation,
    setEdited,
    setErrors,
  } = props;

  const keyedAssets = keyBy(assetData.map(({
    continuous,
    taxable,
    balance,
    shares,
    price,
    name,
    desiredMix,
  }) => convertAssetForRequest({
    name,
    continuous,
    taxable,
    ...(continuous && { balance }),
    ...(!continuous && { shares, price }),
    desiredMix,
  })), 'name');

  const mix = keyBy(assetData.map(({ name, desiredMix = 0.0 }) => ({ name, desiredMix })), 'name');
  Object.keys(mix).forEach((asset) => {
    mix[asset] = mix[asset].desiredMix;
  });

  // Need to validate data first.

  const updateAssets = ({ data }) => {
    console.log(data);

    let { contributions, converged } = data;
    if (data.contributions === null) {
      contributions = {};
    }

    const cloned = assetData.map((asset) => {

      const { deltaBalance, deltaShares } = convertAssetForResponse(
        contributions[asset.name] || { deltaBalance: 0, deltaShares: 0 }
      );
      return {
        ...asset,
        deltaBalance,
        deltaShares,
        ...(!asset.continuous && { requestPrice: asset.price }),
      };
    });

    if (contribute) {
      setRemainder(data.remainder / 100.0);
      setWithdrawn(undefined);
    } else {
      setWithdrawn(data.withdrawn && data.withdrawn / 100.0);
      setRemainder(undefined);
    }
    if (converged === false) {
      setErrors(['Calculation did not converge. Results may be inaccurate.']);
    }
    setLastCalculation('rebalance');
    setEdited(false);
    setThinking(false);
    return setAssetData(cloned);
  }

  const catcher = (error) => {
    const message = axiosError(error);
    setErrors([{ state: false, message }]);
    setThinking(false);
  };

  const calculateRebalance = () => {
    if (thinking) {
      // Button was already clicked. Do not send another request.
      return;
    }

    // Set the loading icon.
    setThinking(true);

    // Validate data first.
    const errors = validateSubmission(assetData);
    if (errors.length > 0) {
      setThinking(false);
      return setErrors(errors);
    }

    post(
      `${process.env.REACT_APP_API}/rebalance`,
      {
        funds: keyedAssets,
        ...(contribute ? { contribution: Math.round(100 * amount) } : { withdraw: Math.round(100 * amount) }),
      }
    ).then(updateAssets)
      .catch(catcher);
  };

  return (
    <Button
      className={classes.button}
      variant='contained'
      color='secondary'
      onClick={calculateRebalance}
    >
      <div className={classes.buttonContent}>
        <CircularProgress
          size={25}
          thickness={4.5}
          className={classes.thinking}
          style={{ visibility: thinking ? 'visible' : 'hidden' }}
        />
        <span className={classes.buttonText}>Rebalance portfolio</span>
      </div>
    </Button>
  );
}

export default errorWrapper(
  RebalanceButton,
  'Rebalance error',
  'Please fix the following issues and resubmit:',
);
