import { Grid, Switch, FormControlLabel } from '@material-ui/core';

export default function Continuous(props) {
  const {
    assetData,
    index,
    updateAssetData,
  } = props;

  const { continuous = true } = assetData[index];

  const updateContinuous = () => {
    updateAssetData({ continuous: !continuous }, index);
  };

  return (
    <Grid item xs={12} sm={6} md={4}>
      <FormControlLabel
        control={<Switch checked={continuous} onChange={updateContinuous} />}
        label={'Mutual fund?'}
        labelPlacement='start'
      />
    </Grid>
  );
}
