import React from 'react';
import { Grid, Box, TextField, makeStyles } from '@material-ui/core';
import { flow } from 'lodash';

import {
  bound,
  onlyMath,
  roundOut,
  toZero,
} from '../../../utils';
import { validateEval, validateField } from '../../../utils/validation';

const useStyles = makeStyles(() => ({
  root: {
    width: '170px',
  },
}));

export default function Shares(props) {
  const {
    assetData,
    index,
    updateAssetData,
  } = props;

  const classes = useStyles();

  const { shares = 0.0, continuous = true } = assetData[index];

  const [err, setErr] = React.useState(validateEval(shares).message);

  const updateShares = (shares) => {
    setErr(undefined);
    updateAssetData({ shares }, index);
  };

  const onFocus = (event) => event.target.select();

  const onChange = (event) => flow([
    onlyMath,
    updateShares])(event.target.value);

    const onBlur = (event) => validateField(
      // Validation of expression evaluation.
      [validateEval],
      // Success callback computes the balance state update.
      flow([
        eval,
        toZero,
        (val) => roundOut(val, 3),
        (val) => bound(val, { lb: 0.0 }),
        updateShares,
      ]),
      // Error callback creates indicates the expression is invalid.
      (value, checked) => setErr(checked[0].message),
    )(event.target.value);

  return (
    <Box 
      component={Grid} 
      item 
      xs={12} 
      sm={6} 
      md={4}
      {...(continuous && { display: 'none' })}
    >
      <TextField 
        error={Boolean(err)}
        helperText={err}
        className={classes.root}
        variant='filled' 
        label='Shares' 
        onFocus={onFocus}
        onChange={onChange}
        onBlur={onBlur}
        value={shares}
      />
    </Box>
  );
}
