import { Grid, Switch, FormControlLabel } from '@material-ui/core';

export default function Taxable(props) {
  const {
    assetData,
    index,
    updateAssetData,
  } = props;

  const { taxable = true } = assetData[index];

  const updateTaxable = () => {
    updateAssetData({ taxable: !taxable }, index);
  };

  return (
    <Grid item xs={12} sm={6} md={4}>
      <FormControlLabel
        control={<Switch checked={taxable} onChange={updateTaxable} />}
        label={'Taxable?'}
        labelPlacement='start'
      />
    </Grid>
  );
}
