import React from 'react';
import { TextField, InputAdornment, makeStyles } from '@material-ui/core';
import { flow } from 'lodash';

import Selection from './Selection';
import CalculationStatus from './CalculationStatus';
import Strategy from './Strategy';

import {
  onlyMath,
  roundOut,
  toZero,
} from '../../utils';
import { validateEval, validateField } from '../../utils/validation';

const useStyles = makeStyles({
  root: {
    width: '100%',
    paddingTop: '30px',
    paddingBottom: '30px',
    paddingLeft: '20px',
    paddingRight: '19px',
    display: 'flex',
    alignItems: 'flex-end',
    justifyContent: 'space-between',
    flexWrap: 'wrap',
  },
  leftSide: {
    display: 'flex',
    flexDirection: 'column',
  },
  amount: {
    marginTop: '10px',
    width: '100%',
    maxWidth: '300px',
  },
});

export default function ContribWithdrawField(props) {
  const classes = useStyles();

  const {
    amount = 0.0,
    setAmount,
    contribute = true,
    setContribute,
    remainder,
    withdrawn,
    lastCalculation,
    edited,
    setEdited,
    strategy,
    setStrategy,
  } = props;

  const [err, setErr] = React.useState(validateEval(amount).message);

  const updateAmount = (amount) => {
    setErr(undefined);
    setAmount(amount);
    if (typeof amount === 'number') {
      window.localStorage.setItem('amount', amount);
    }
    setEdited(true);
  };

  const rectify = (amount) => {
    if (amount < 0) {
      setContribute(!contribute);
      window.localStorage.setItem('contribute', !contribute);
    }
    return Math.abs(amount);
  };

  const onFocus = (event) => event.target.select();

  const onChange = (event) => flow([
    onlyMath,
    updateAmount])(event.target.value);

  const onBlur = (event) => validateField(
    // Validation of expression evaluation.
    [validateEval],
    // Success callback computes the balance state update.
    flow([
      eval,
      toZero,
      rectify,
      (val) => roundOut(val, 2),
      updateAmount,
    ]),
    // Error callback creates indicates the expression is invalid.
    (value, checked) => setErr(checked[0].message),
  )(event.target.value);

  return (
    <div className={classes.root}>
      <div className={classes.leftSide}>
        <Selection
          contribute={contribute}
          setContribute={setContribute}
          setEdited={setEdited}
        />
        <TextField
          error={Boolean(err)}
          helperText={err}
          className={classes.amount}
          variant='filled'
          label='amount'
          value={amount}
          onFocus={onFocus}
          onChange={onChange}
          onBlur={onBlur}
          InputProps={{
            startAdornment: <InputAdornment position="start">$</InputAdornment>,
          }}
        />
        <Strategy
          contribute={contribute}
          strategy={strategy}
          setStrategy={setStrategy}
          setEdited={setEdited}
        />
      </div>
      <CalculationStatus
        remainder={remainder}
        withdrawn={withdrawn}
        lastCalculation={lastCalculation}
        edited={edited}
      />
    </div>
  );
}
