import { Card, CardContent } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

import Delete from './Delete';
import EditButton from './EditButton';
import Ticker from './Ticker';
import Result from './Result';

const useStyles = makeStyles(() => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'flex-start',
  },
  content: {
    display: 'flex',
    justifyContent: 'flex-start',
    alignItems: 'center',
  },
  assetElement: {
    flexBasis: '75px',
    width: '100%',
    alignItems: 'center',
    marginTop: '5px',
    marginLeft: '1%',
    marginRight: '1%',
  },
}));

export default function AssetList(props) {
  const {
    assetData,
    updateAssetData,
    deleteAsset,
    edited,
  } = props;

  const classes = useStyles();

  return (
    <div>
      {assetData.map((props, index) => (
        <Card key={`asset-${index}`} className={classes.assetElement} elevation={3}>
          <CardContent className={classes.content}>
            <Delete index={index} deleteAsset={deleteAsset} />
            <EditButton
              index={index}
              assetData={assetData}
              updateAssetData={updateAssetData}
            />
            <Ticker name={props.name} />
            <Result
              continuous={props.continuous}
              deltaBalance={props.deltaBalance}
              deltaShares={props.deltaShares}
              price={props.requestPrice}
              edited={edited}
            />
          </CardContent>
        </Card>
      ))}
    </div>
  );
}
