import React from 'react';
import { Grid, TextField, makeStyles } from '@material-ui/core';
import { Autocomplete } from '@material-ui/lab';

import { validateName } from '../../../utils/validation';

const useStyles = makeStyles(() => ({
  root: {
    width: '170px',
  },
}));


export default function Ticker(props) {
  const {
    assetData,
    index,
    updateAssetData,
  } = props;

  const classes = useStyles();

  const { name = '' } = assetData[index];

  const [err, setErr] = React.useState(validateName(name, assetData, index).message);

  const updateName = (event, values) => {
    const newName = values || event.target.value;
    updateAssetData({ name: newName }, index);
    setErr(validateName(newName, assetData, index).message);
  }

  return (
    <Grid item xs={12} sm={6} md={4}>
      <Autocomplete
        className={classes.root}
        value={name}
        options={['$cash']}
        getOptionLabel={(option) => option}
        getOptionSelected={() => true}
        onChange={updateName}
        renderInput={(params) =>
          <TextField
            error={Boolean(err)}
            helperText={err}
            {...params}
            variant='filled'
            label='Ticker symbol'
            onChange={updateName}
            value={name}
          />}
      />
    </Grid>
  );
}
