import React from 'react';

import { Button, CircularProgress, makeStyles } from '@material-ui/core';
import { keyBy } from 'lodash';
import { post } from 'axios';

import { convertAssetForRequest, convertAssetForResponse } from '../../utils';

import errorWrapper from '../ErrorWrapper';
import { validateSubmission } from '../../utils/validation';
import { axiosError } from '../../utils/errors';

const useStyles = makeStyles(() => ({
  button: {
    marginBottom: '5px',
  },
  buttonContent: {
    display: 'flex',
    alignItems: 'center',
  },
  thinking: {
    color: 'rgb(230,230,230)',
  },
  buttonText: {
    paddingLeft: '10px',
    paddingRight: '30px',
  },
}));

function CalculateContributionButton(props) {
  const classes = useStyles();

  const [thinking, setThinking] = React.useState(false);

  const {
    assetData,
    contribute,
    amount,
    strategy,
    setAssetData,
    setRemainder,
    setWithdrawn,
    setLastCalculation,
    setEdited,
    setErrors,
  } = props;

  // Note that a traditional contribute ignores balances/shares.
  const keyedAssets = keyBy(assetData.map(({
    continuous,
    taxable,
    balance,
    shares,
    price,
    name,
    desiredMix,
  }) => convertAssetForRequest({
    name,
    continuous,
    taxable,
    ...(continuous && {
      balance: (contribute && strategy === 'traditional') ?
        0.0 : balance
    }),
    ...(!continuous && {
      shares: (contribute && strategy === 'traditional') ? 0 : shares,
      price
    }),
    desiredMix,
  })), 'name');

  const mix = keyBy(assetData.map(({ name, desiredMix = 0.0 }) => ({ name, desiredMix })), 'name');
  Object.keys(mix).forEach((asset) => {
    mix[asset] = mix[asset].desiredMix;
  });

  const updateAssets = ({ data }) => {
    console.log(data);

    let { contributions, converged } = data;
    if (data.contributions === null) {
      contributions = {};
    }

    const cloned = assetData.map((asset) => {

      const { deltaBalance, deltaShares } = convertAssetForResponse(
        contributions[asset.name] || { deltaBalance: 0, deltaShares: 0 }
      );
      return {
        ...asset,
        deltaBalance,
        deltaShares,
        ...(!asset.continuous && { requestPrice: asset.price }),
      };
    });

    if (contribute) {
      setRemainder(data.remainder / 100.0);
      setWithdrawn(undefined);
      setLastCalculation('contribution');
    } else {
      setWithdrawn(data.withdrawn && data.withdrawn / 100.0);
      setRemainder(undefined);
      setLastCalculation('withdrawal');
    }
    if (converged === false) {
      setErrors(['Calculation did not converge. Results may be inaccurate.']);
    }
    setEdited(false);
    setThinking(false);
    return setAssetData(cloned);
  }

  const catcher = (error) => {
    const message = axiosError(error);
    setErrors([{ state: false, message }]);
    setThinking(false);
  };

  const calculateContribution = () => {
    post(
      `${process.env.REACT_APP_API}/contribute`,
      {
        funds: keyedAssets,
        contribution: Math.round(100 * amount),
      },
    ).then(updateAssets)
      .catch(catcher);
  };

  const calculateWithdrawal = () => {
    post(
      `${process.env.REACT_APP_API}/withdraw`,
      {
        funds: keyedAssets,
        withdraw: Math.round(100 * amount),
      },
    ).then(updateAssets)
      .catch(catcher);
  }

  const onClick = () => {
    if (thinking) {
      // Button was already clicked. Do not send another request.
      return;
    }

    // Set the loading icon.
    setThinking(true);

    // Validate data first.
    const errors = validateSubmission(assetData);
    if (errors.length > 0) {
      setThinking(false);
      return setErrors(errors);
    }

    if (contribute) {
      return calculateContribution();
    }
    return calculateWithdrawal();
  }

  return (
    <>
      <Button
        className={classes.button}
        variant='contained'
        color='secondary'
        onClick={onClick}
      >
        <div className={classes.buttonContent}>
          <CircularProgress
            size={25}
            thickness={4.5}
            className={classes.thinking}
            style={{ visibility: thinking ? 'visible' : 'hidden' }}
          />
          <span className={classes.buttonText}>Calculate contribution/withdrawal</span>
        </div>
      </Button>
    </>
  );
}

export default errorWrapper(
  CalculateContributionButton,
  'Contribution/withdrawal error',
  'Please fix the following issues and resubmit:',
);
