import { ButtonGroup } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

import AddButton from './AddButton';
import CalculateContributionButton from './CalculateContributionButton';
import RebalanceButton from './RebalanceButton';

const useStyles = makeStyles((theme) => ({
  div: {
    marginLeft: 'auto',
    marginRight: 'auto',
  },
  buttonGroup: {
    width: '100%',
    maxWidth: '400px',
    paddingTop: '15px',
  },
}));

export default function Controls(props) {
  const {
    assetData,
    contribute,
    amount,
    strategy,
    setAssetData,
    addAsset,
    updateAssetData,
    setRemainder,
    setWithdrawn,
    setLastCalculation,
    setEdited,
  } = props;

  const classes = useStyles();

  return (
    <div className={classes.div}>
      <ButtonGroup orientation='vertical' className={classes.buttonGroup}>
        <AddButton
          addAsset={addAsset}
          assetData={assetData}
          updateAssetData={updateAssetData}
          setEdited={setEdited}
        />
        <CalculateContributionButton
          assetData={assetData}
          contribute={contribute}
          amount={amount}
          strategy={strategy}
          setAssetData={setAssetData}
          setRemainder={setRemainder}
          setWithdrawn={setWithdrawn}
          setLastCalculation={setLastCalculation}
          setEdited={setEdited}
        />
        <RebalanceButton
          assetData={assetData}
          contribute={contribute}
          amount={amount}
          setAssetData={setAssetData}
          setRemainder={setRemainder}
          setWithdrawn={setWithdrawn}
          setLastCalculation={setLastCalculation}
          setEdited={setEdited}
        />
      </ButtonGroup>
    </div>
  );
}
